<template>
    <div class="wrap">
      <footerx :cartNum="cartNum" :cart="cart" @goTop="top"></footerx>
        <div class="index">
                <div>
                    <a href="#">
                        <router-link to="/Home2/Home">></router-link>
                    </a>
                    <a href="#">
                        <router-link to="/Home2/CustomerOrders">></router-link>
                    </a>
                    <a href="#"></a>
                </div>
                
            </div>
        <loading :active.sync="isLoading"></loading>

        <!-------------------------------------------------------------->
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts">
              <td class="align-middle">
                <button type="button" class="btn btn-outline-danger btn-sm"
                 @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                <img :src="item.product.imageUrl" >
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  
                </div> 
              </td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right"></td>
              <td class="text-right">{{ cart.total }}</td>
            </tr>

            <!--------------------------------------------->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success"></td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary"  type="button" @click="addCouponCode" >
              
            </button>
          </div>
        </div>
      


      <div class="my-5 row justify-content-center">
        <form class="col-md-6" @submit.prevent="createOrder">
          <div class="form-group">
            <label for="useremail">Email</label>
            <ValidationProvider name="email" rules="required|email" v-slot="{ errors }">

              <input type="email" class="form-control" name="email" id="useremail"
                v-model="form.user.email" placeholder=" Email" required>
              <span class="text-danger">{{ errors[0] }}</span>
              
            </ValidationProvider>
            
          </div>


    <!----------------- component rules main.js---------------------------->
    <!-----------------errors[0]  messages---------------------------->
    <!-----------------"min:3" 33---------------------------->
      
          <div class="form-group">
            <label for="username"></label>

            <ValidationProvider rules="min:3" v-slot="{ errors }">
              <input type="text" class="form-control" name="name" id="username"
                v-model="form.user.name" placeholder="" required
                :class="{'is-invalid' : errors[0]}">
              <span class="text-danger" v-if="!form.user.name"></span> 
              <span class="text-danger" v-if="form.user.name">{{ errors[0] }}</span>  
            </ValidationProvider>   
          </div>
        
          <div class="form-group">
            <label for="usertel"></label>
            <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="">
          </div>
        
          <div class="form-group">
            <label for="useraddress"></label>
            <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
              placeholder="">
            <span class="text-danger" v-if="!form.user.address"></span>
          </div>
        
          <div class="form-group">
            <label for="comment"></label>
            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-danger"></button>
          </div>
        </form>
        </div>



          <!-- Modal -->
      <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            </div>
          </div>
        </div>
      </div>



    </div>
</template>



<style scoped>
.index{
    width:1063px;
    height:40px;
    margin-left: auto ;
    margin-right: auto;
    padding:10px;
    background: #e7eeea;
    border-radius:10px;
}
.index a{
    color:#37523d;
}
.wrap{
    width:1063px;
    margin-left: auto ;
    margin-right: auto;
}
.align-middle img{
  width:192.5;
  height:125px;
}
</style>
<script>
import $ from 'jquery';
import footerx from '../footerx';

export default {
  components: {
        footerx,
    },
    data() {
      return {
        cart:[], //
        cartNum:'',
        form: {  //
            user: {
            name: '',
            email: '',
            tel: '',
            address: '',
            },
            message: '',
        },
        isLoading: false, // ()
        coupon_code:'',
        };
    },


    methods: {
      getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        // vm.products = response.data.products;
        console.log(response.data.data, '');
        vm.isLoading = false;
        vm.cart = response.data.data;
        vm.cartNum = response.data.data.carts.length;
        });
      },
      // 
      removeCartItem(id) {
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
        vm.isLoading = true;
        this.$http.delete(url).then((response) => {
            vm.getCart();  //
            console.log(response, '');
            vm.isLoading = false;
            $('#exampleModal').modal('show');
        });
      },
      // 
      addCouponCode() {
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
        //API
        const coupon = {
            code: vm.coupon_code,
        };
        vm.isLoading = true;
        this.$http.post(url, { data: coupon }).then((response) => {
            vm.getCart();  //
            console.log(response);
            vm.isLoading = false;
        });
      },
      //
      createOrder() {
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
        const order = vm.form;
        vm.isLoading = true;
        this.$http.post(url, { data: order }).then((response) => {
            console.log(response);
            if(response.data.success) {
            vm.$router.push(`/Home2/customerOrders/${response.data.orderId}`)
            }
            // vm.getCart();
            vm.isLoading = false;
        });
      },
      top(){
        $("html,wrap").animate({
            scrollTop: 0
        }, 700);
      },
    },
    created() {
      this.getCart();
      this.top();
  },
}
</script>


