// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios' //AJAX
import VueAxios from 'vue-axios' // VUe
import Loading from 'vue-loading-overlay'; //  - 
import 'vue-loading-overlay/dist/vue-loading.css';  //  - 
import 'bootstrap';



// --------------vee-validate------------------------
// https://hao1229.github.io/2019/08/09/EcommercePractice8/

//
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
// 
import { localize } from 'vee-validate'
import TW from 'vee-validate/dist/locale/zh_TW.json'
localize('zh_TW', TW)


// Add a rule'secret' value  'example' message 
extend('min', {
  validate (value, args) {
    return value.length >= args.length    
  },
  params: ['length'],
  message: ''
})
// Register it globally
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);


// email 
import { required, email } from 'vee-validate/dist/rules'

extend('email', email)

extend('required', { //  email 
  ...required,
  message: ''
})
// --------------vee-validate------------------------






import App from './App'
import router from './router'
import './bus'; //
import currencyFilter from './Filters/currency'; //
import dateFilter from './Filters/date'; //


Vue.use(VueAxios, axios)
Vue.config.productionTip = false

// 
Vue.component('Loading', Loading); //  - 
Vue.filter('currency', currencyFilter); // :1. 2.import
Vue.filter('date', dateFilter); // :1. 2.import


// API  ()
axios.defaults.withCredentials = true;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
});


// Vue Router 
// 
// to: Route:  

// from: Route: 

// next: Function: 
router.beforeEach((to, from, next) => {
  console.log(to, from, next);
  // requiresAuth 
  // API
  if(to.meta.requiresAuth){
    console.log('');
    //    API 
    const api = `${process.env.APIPATH}/api/user/check`;
    // this.$http 
    //axios
    axios.post(api).then((response) =>{
    // console.log(response.data);
    //  
    if (response.data.success){
      next();
      console.log('');
    }else{ //
      next({
        path: '/login',
      });
      console.log('');
    }
    });
  }else{  //
    next();
  }
})


