<template>
    <div>
        <footerx :cartNum="cartNum" :cart="cart" @goTop="top"></footerx>
        <loading :active.sync="isLoading"></loading>
        
        <div class="wrap">
            <div class="index">
                <div>
                    <a href="#">
                        <router-link to="/Home2/Home">></router-link>
                    </a>
                    <a href="#">
                        <router-link to="/Home2/CustomerOrders">></router-link>
                    </a>
                    <a href="#">{{ product.title }}</a>
                </div>
                
            </div>
            <h3>{{ product.title }}</h3>

            <div class="product">

                <div class="productImg">
                    <img  :src="product.imageUrl" class="img-fluid" alt="">
                    <div class="productRemarks">
                        <h5></h5>
                        <ul>    
                            <li> </li>
                            <li> .</li>
                            <li> 2019-05-17 15:28:10</li>
                            <li></li>
                        </ul>
                    </div>
                    
                </div>

                <div>
                    <div class="productManu">
                        <div class="productManu-0">
                            <h6>{{ product.content }}</h6>
                            <h6>{{ product.description }}</h6>
                        </div>
                        <div class="productManu-1">
                            <div class="originPriceX" v-if="!product.price">{{ product.origin_price | currency }} </div>
                            
                            <div class="originPrice" v-if="product.price">
                                <h4> {{ product.origin_price | currency }} </h4>
                            </div>
                            <div class="price" v-if="product.price">
                                <h4> {{ product.price | currency }} </h4>
                            </div>
                        </div>
                        <div class="productManu-2">
                            <select name="" class="form-control mt-3" v-model="product.num">
                                <option :value="num" v-for="num in 10" :key="num">
                                     {{num}} {{product.unit}}
                                </option>
                            </select>
                        </div>
                        <div class="productManu-3">
                            <h6>{{ product.num * product.price | currency }}</h6>
                            <button type="button" class="btn btn-primary"
                                @click="addtoCart(product.id, product.num)">
                            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                            
                            </button>
                        </div>
                        
                        
                    </div>
                    <div class="productManu-4">
                        <p></p>
                    </div>
                    <div class="productManu-5">
                        <h6>:</h6>
                        <ul>
                            <li><img src="https://upload.cc/i1/2020/08/15/Z9u1wK.png"></li>
                            <li><img src="https://upload.cc/i1/2020/08/15/5cdIyr.png">7-11</li>
                            <li></li>
                            <li></li>
                            
                        </ul>
                    </div>
                    <div class="productManu-5">
                        <h6>:</h6>
                        <ul>
                            <li><img src="https://upload.cc/i1/2020/08/15/Z9u1wK.png"> 60 </li>
                            <li><img src="https://upload.cc/i1/2020/08/15/5cdIyr.png">7-11 60 </li>
                            <li>/ 100 </li>
                            <li> 130 </li>
                            
                        </ul>
                        <div class="productManu-5Button">
                        <router-link to="/Home2/shoppingCart">
                            <button type="button" class="btn btn-primary">
                                <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                                
                            </button>
                        </router-link>
                        </div>
                        
                    </div>
                </div>
                

                
                
            </div>

            <div class="Popular">
                <h5></h5>
            </div>
            <div class="center-1-flex" >
                <div v-for="item in products">    
                    <a href="#" @click="getProduct(item.id)" >
                        <img :src="item.imageUrl" class="d-block w-100" alt="..." v-if="!isLoading">
                        <h6>{{ item.title }}</h6>
                    </a>
                </div>    
                    
            </div>
            

                <!--
              <div class="modal-dialog" role="document">
                  <div class="modal-content">
                      <div class="modal-header">
                          <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
                          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                          </button>
                      </div>
                      <div class="modal-body">
                          <img :src="product.imageUrl" class="img-fluid" alt="">
                          <blockquote class="blockquote mt-3">
                          <p class="mb-0">{{ product.content }}</p>
                          <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                          </blockquote>
                          <div class="d-flex justify-content-between align-items-baseline">
                          <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
                          <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
                          <div class="h4" v-if="product.price"> {{ product.price }} </div>
                          </div>
                          <select name="" class="form-control mt-3" v-model="product.num">
                          <option :value="num" v-for="num in 10" :key="num">
                               {{num}} {{product.unit}}
                          </option>
                          </select>
                      </div>
                      <div class="modal-footer">
                          <div class="text-muted text-nowrap mr-3">
                           <strong>{{ product.num * product.price }}</strong> 
                          </div>
                          <button type="button" class="btn btn-primary"
                            @click="addtoCart(product.id, product.num)">
                          <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                          
                          </button>
                      </div>
                  </div>
              </div>
              -->


                    <!-- Modal -->
                <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                        </div>
                        <div class="modal-body">
                        
                        </div>
                        <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
                        </div>
                    </div>
                    </div>
                </div>



              
        </div>
    </div>
</template>
<style scoped>
ul, li{
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
ol, ul {
    list-style: none;
}
.index{
    height:40px;
    padding:10px;
    background: #e7eeea;
    border-radius:10px;
    margin-bottom:15px;
}
.index a{
    color:#37523d;
}
.wrap{
    
    width:1063px;
    margin-left: auto ;
    margin-right: auto;
}
.product{
    display:flex;
    justify-content:space-around;
}
.product img{
    box-shadow:0px 0px 9px #e7eeea;
}
.productRemarks{
    margin-top:20px;
}
.productRemarks h5{
    padding-left:10px;
}
.productRemarks li{
    margin-top:10px;
    padding-left:10px;
}
.productManu{
    margin-left:20px;
    border-radius:10px;
    width:600px;
    background: #e7eeea;
    padding:15px;
}
.productManu-0{
    display:flex;
    justify-content:space-between;
    
}
.productManu-1{
    display:flex;
    justify-content:space-between;
    margin-top:10px;
    
}
.originPriceX{
    
}
.originPrice{
    text-decoration:line-through;
}
.productManu-2{
    width:150px;
}

.productManu-3{
    display:flex;
    justify-content:flex-end;
    margin-top:20px;
}
.productManu-3 h6{
    margin:10px;
}

.productManu-4{
    margin-left:20px;
    margin-top:10px;
    border-bottom:1px solid #cccccc;
}
.productManu-5{
    display:flex;
    margin-top:20px;
    margin-left:20px;
}
.productManu-5 img{
    width:20px;
    height:20px;
}
.productManu-5 li{
    padding-left:10px;
    margin-bottom:10px;
}
.productManu-5Button{
    margin-left:auto;
    margin-top:auto;
}

.Popular h5{
    margin-top:30px;
    padding-top:15px;
    border-top: 1px solid #cccccc;
    text-align:center;
}
.center-1-flex {
    display:flex;
    margin-left: auto;
    margin-right: auto;
}

.center-1-flex a{
    
    display:block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom:50px;
    padding:5px;
}
.center-1-flex img{
    box-shadow:0px 0px 9px #e7eeea;
}
.center-1-flex h6{
    font-size:13px;
    color:#37523d;
    text-align:center;
}
</style>

<script>
import $ from 'jquery';

import footerx from '../footerx';
export default {
    components: {
        footerx,
    },
    data() {
        return {
            id: '',
            product:[],
            products:[],
            pagination:[],
            cart:[],
            cartNum:'',
            status: {
                loadingItem: '', //item id 
            },
            isLoading: false,
        }
    },
    methods:{
        // 
        getProducts(page = 1){
          // ?page=page 
            const api = `${process.env.APIPATH}/API/${process.env.CUSTOMPATH}/products?page=${page}`;
            // console.log(process.env.APIPATH, process.env.CUSTOMPATH);
            // main.jsAJAXAJAX
            const vm = this;
            vm.isLoading = true; //
            this.$http.get(api).then((response) =>{
              console.log(response.data)
              vm.isLoading = false;  //
              vm.products = response.data.products;
              vm.pagination = response.data.pagination; //pagination
              console.log(vm.products[0].imageUrl);

            });
        },

        // API
        getProduct(id) {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            vm.status.loadingItem = id; 
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                console.log(response);
                if(response.data.success) {
                    vm.$router.push(`/Home2/productDetails/${response.data.product.id}`)
                }
                vm.isLoading = false;
                vm.product = response.data.product;
                //$('#productModal').modal('show');
                
                vm.status.loadingItem = '';
            });
        },
        // API
        addtoCart(id, qty = 1) {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.status.loadingItem = id; 
            const cart = {    //1.ID 2.
                product_id: id,
                qty,
            };
            this.$http.post(url, { data: cart }).then((response) => {
                console.log(response);
                vm.status.loadingItem = '';
                vm.getCart();
                $('#exampleModal').modal('show');
            });
        },
        // 
        getCart() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                // vm.products = response.data.products;
                console.log(response.data.data, '');
                vm.isLoading = false;
                vm.cart = response.data.data;
                vm.cartNum = response.data.data.carts.length;
            });
        },
        top(){
        $("html,wrap").animate({
            scrollTop: 0
        }, 700);
      },
      
    },

    created() {
        this.id = this.$route.params.id;  //routeid
        this.getProduct(this.id);
        this.getCart();
        this.getProducts();
        this.top();
    },
}
</script>


